<template>
  <div class="vipcard_container">
    <head-top head-title="会员中心" goBack="true"></head-top>
    <section class="vip_cart" v-if="userInfo">
        <header>为账户<span>{{userInfo.username}}</span>购买会员</header>
        <section class="vip_1">
            <router-link class="vip_1_tx" to="/vipcard/vipDescription">
                <span>会员特权</span>
                <div to="" class="vup_1_img"> 
                    <span>
                        会员说明
                    </span>
                    <svg fill="#ccc">
                        <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                    </svg> 
                </div>
            </router-link>
            <section class="vip_1_center">
                <div class="vip_1_center_img">
                    <img src="../../images/sheng.jpeg" alt="">
                </div>
                <div class="vip_1_center_tx">
                    <h4>减免配送费</h4>
                    <p>每月减免30单，每日可减免3单，每单最高减4元</p>
                    <p>蜂鸟专送</p>
                </div>
            </section>
            <section class="vip_1_center">
                <div class="vip_1_center_img">
                    <img src="../../images/jifen.jpeg" alt="">
                </div>
                <div class="vip_1_center_tx">
                    <h4>减免配送费</h4>
                    <p>每月减免30单，每日可减免3单，每单最高减4元</p>
                    <p>蜂鸟专送</p>
                </div>
            </section>
        </section>
        <section class="vip_2">
            <div class="vip_2_tx">
                <span>开通会员</span>
            </div>
            <div class="vip_2_img">
                <div class="vip_2_img_tx">
                    <span>1个月</span>
                    <span>￥20</span>
                </div>
                <div class="vip_2_img_bottom" @click="dingdan">
                    <span>购买</span>
                </div>
            </div>
        </section>
        <section class="vip_3">
           <router-link to="/vipcard/useCart" class="vip_3_body">
                <div class="vip_3_tx">
                    兑换会员
                </div>
                <div class="vip_3_img">
                    <span>
                        使用卡密卡号
                    </span>
                    <svg fill="#ccc">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                    </svg> 
                </div>
           </router-link>
        </section>
        <section class="vip_3">
           <router-link to="/vipcard/invoiceRecord" class="vip_3_body">
                <div class="vip_3_tx">
                    购买记录
                </div>
                <div class="vip_3_img">
                    <span>
                        开发票
                    </span>
                    <svg fill="#ccc">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                    </svg> 
                </div>
           </router-link>
        </section>
    </section>
    <transition name="vipDescription_view" mode="out-in">
        <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import headTop from '../../components/header/header'
import {mapState,mapMutations} from 'vuex'
import {getOrderList} from '../../service/getData'
export default {
    components: {
        headTop
    },computed: {
        ...mapState([
            'userInfo'
        ])
    },methods: {
        ...mapMutations([
            'ORDER_SUCCESS','BUY_CART'
        ]),
        dingdan()
        {
            this.ORDER_SUCCESS({order_id: '399525134200981325'});
            this.BUY_CART(20);
            this.$router.push('/confirmOrder/payment')
        }
    }
}
</script>

<style lang="scss" scopde>
@import '../../style/mixin.scss';
.vipcard_container{
    padding-top: 1.95rem;
}
.vip_cart{
    header{
            padding:0.4rem;
        @include sc(0.6rem,#666);
        span{
            font-weight: bold;
        }
    }
    .vip_1{
        background: #fff;
        .vip_1_tx{
            padding:0.7rem 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 0.025rem solid #f1f1f1;
            margin-left:0.6rem;
            span{
                @include sc(0.6rem,#333);
            }
            .vup_1_img{
                display: flex;
                align-items: center;
                svg{
                    margin-left:0.4rem;
                    @include wh(.5rem,.5rem);
                }
            }
        }
    }
}
.vip_1_center{
    padding:0.7rem 0;
    display: flex;
    align-items: center;
    border-bottom:0.025rem solid #f1f1f1;
    margin-left: .5rem;
    .vip_1_center_img{
        flex:1;
        img{
            @include wh(1.8rem,1.8rem);
        }
    }
    .vip_1_center_tx{
        flex:5;
        h4{
            @include sc(0.6rem,#333);
            margin-top: .2rem;
        }
        p{
            @include sc(0.5rem,#333);
            line-height: .8rem;
        }
    }
}
.vip_2{
    margin-top: 0.4rem;
    background: #fff;
    .vip_2_tx{    
        padding:0.4rem 0;
        margin-left: 0.4rem;
        border-bottom: 0.025rem solid #f1f1f1;
        span{
            @include sc(0.8rem,#666);
        }
    }
    .vip_2_img{
        padding:0.8rem 0;
        margin-left: 0.4rem;
        display: flex;
        justify-content: space-between;
        @include sc(0.5rem,#333);
        align-items: center;
        .vip_2_img_tx{
            display: flex;
            align-items: center;
            font-size: 0;
            span:nth-of-type(1){
                @include sc(.6rem,#333);
            }
            span:nth-of-type(2)
            {
                @include sc(.6rem,#333);
                color:#f60;
            }
        }
        .vip_2_img_bottom{
            span{
                border:0.025rem solid #f60;
                padding:0.3rem 0.9rem;
                border-radius: 0.2rem;
                margin-right: 0.7rem;
                color:#f60;
            }
        }
    }
}
.vip_3{
    margin-top: .4rem;
    background: #fff;
    .vip_3_body{
        margin-left:.4rem;
        padding:.6rem 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .vip_3_tx{
            @include sc(.5rem,#666);
        }
        .vip_3_img{
            display: flex;
            align-items: center;
            padding-right: 0.4rem;
            span{
                padding-right: 0.3rem;
                @include sc(.5rem,#666);
            }
            svg{
                @include wh(.5rem,.5rem);
            }
        }
    }
}
.vipDescription_view-enter-active,.vipDescription_view-leave-active{
    transition: all .4s;
}
.vipDescription_view-enter,.vipDescription_view-leave-active{
    transform: translate3d(2rem,0,0);
    opacity: 0;
}
</style>



